h3 {
  text-align: center;
  font-weight: 400;
}

input {
  width: 95%;
  margin: 0 12px;
  padding: 3px;
  height: 25px;
}

.inputDiv {
  overflow: hidden;
}

ul {
  padding: 0;
}

ul li {
  list-style: none;
  border-top: 1px solid gainsboro;
  padding: 12px;
  display: flex;
  justify-content: space-between;
}

ul li p {
  margin: 0;
  color: gainsboro;
  display: none;
}

ul li:hover {
  background-color: rgb(242, 242, 242);
  cursor: pointer;
}

ul li:hover p {
  display: block;
}